import React, { useState } from 'react'

export default function TextForm(props) {
    const handleUpClick = () => {
        // console.log("Uppercase was clicked " + text);
        let newText = text.toUpperCase();
        setText(newText)
        props.showAlert("Converted to Uppercase!", "success");
    }
    const handleLowClick = () => {
        // console.log("Lowercase was clicked " + text);
        let newText = text.toLowerCase();
        setText(newText)
        props.showAlert("Converted to Lowercase!", "success");
    }
    const handleClearText = () => {
        // console.log("Lowercase was clicked " + text);
        let newText = ('');
        setText(newText)
    }
    const handleCopy = () => {
        let text = document.getElementById("myBox");
        text.select();
        navigator.clipboard.writeText(text.value);
    }
    const handleOnchange = (event) => {
        // console.log("Uppercase was clicked");
        setText(event.target.value);
    }
    const [text, setText] = useState('');
    return (
        <>
            <div className="container" style={{color: props.mode === 'dark'?'white':'black'}}>
                <h1>{props.heading}</h1>
                <div className="mb-3">
                    <textarea className="form-control" value={text} onChange={handleOnchange} style={{backgroundColor: props.mode === 'dark'?'#404040':'white', color: props.mode === 'dark'?'white':'black'}} id="myBox" rows="6"></textarea>
                </div>
                <button className="btn btn-primary mx-2" onClick={handleUpClick}>Convert to Uppercase</button>
                <button className="btn btn-primary mx-2" onClick={handleLowClick}>Convert to Lowercase</button>
                <button className="btn btn-danger mx-2" onClick={handleClearText}>Clear Text</button>
                <button className="btn btn-success mx-2" onClick={handleCopy}>CopyText</button>
            </div>
            <div className="container my-3" style={{color: props.mode === 'dark'?'white':'black'}}>
            <div className='p-3 border rounded'>
                <h2>Your Text Summary</h2>
                <p>{text.split(" ").length} Words And {text.length} Characters</p>
                <p>{0.008 * text.split(" ").length} Minutes Read</p>
            </div>
            <div className='p-2 border rounded m-2'>
                <h2>Preview</h2>
                <p>{text.length>0?text:"Enter Somthing In The TextBox Above To Preview It Here"}</p>
            </div>
            </div>
        </>

    )
}
